    export default {

        name: 'voice',
	    
		props: [`theme`, `is_play`, `audio`, `voice_i`],

		data() {
            return {
                animationData1: '',
                animationData2: '',
                animationStatus: false,
                manager: null,
                start_tran: false,
            }
		},
		
		watch: {
            voice_i: {
                handler(data) {
                    if (data >= 30) {
                        this.end();
                    }
                },
            }
        },
		
	    methods: {
            animationFun:function(animationData){
                if(!this.animationStatus){
                    return;
                }
                var animation = uni.createAnimation({
                    duration: 1000
                })
                animation.opacity(0).scale(1.5,1.5).step(); 
                this[animationData] = animation.export();
            },
            animationEnd: function (animationData) {
                var animation = uni.createAnimation({
                    duration: 0
                })
                animation.opacity(1).scale(1, 1).step(); 
                this[animationData] = animation.export()
            },
            recodeEnd: function() {
                //动画1结束
                var animation1 = uni.createAnimation({
                    duration: 0
                })
                animation1.opacity(1).scale(1, 1).step(); 
                //动画2结束
                var animation2 = uni.createAnimation({
                    duration: 0
                })
                animation2.opacity(1).scale(1, 1).step(); 
                this.animationData1 = animation1.export();
                this.animationData2 = animation2.export();
                this.animationStatus = false;
            },
            recodeClick: function() {
                this.animationStatus = true;
                this.animationFun('animationData1');
                setTimeout(()=>{
                    this.animationFun('animationData2');
                },500)
                setTimeout(() => {
                    this.animationRest();
                }, 1000)
            },
            animationRest: function() {
                this.animationEnd('animationData1');
                setTimeout(()=>{
                    this.animationEnd('animationData2');
                },500);
                setTimeout(() => {
                    if (this.animationStatus) {
                        this.recodeClick();
                    }
                }, 100);
            },
            start() {
                this.recodeClick();
                this.$emit('recorder', true);
            },
            end() {
                this.recodeEnd();
                this.$emit('recorder', false);
                this.start_tran = false;
            },
            playaudio() {
                if (this.is_play) {
                    this.$emit('play', false);
                } else {
                    this.$emit('play', true);
                }
            },
            open() {
                let self = this;

                wx.getSetting({
                    success(res) {
                        if (!res.authSetting['scope.record']) {
                            wx.authorize({
                                scope: 'scope.record',
                                success () {
                                    self.start_tran = true;
                                    self.$emit('click');
                                },
                                fail(e) {
                                    wx.showModal({
                                        title: '提示',
                                        content: '您未授权录音，功能将无法使用',
                                        showCancel: true,
                                        confirmText: "授权",
                                        confirmColor: "#52a2d8",
                                        success() {
                                            wx.openSetting({
                                                success: (res) => {
                                                    if (!res.authSetting['scope.record']) {
                                                        
                                                    } else {
                                                        self.start_tran = true;
                                                        self.$emit('click');
                                                    }
                                                },
                                                fail(e) {
                                                }
                                            })
                                        }
                                    })
                                }
                            })
                        } else {
                            self.start_tran = true;
                            self.$emit('click');
                        }
                    },
                    fail(e) {
                    }
                });




            },
            close() {
                this.audio = {}
                this.$emit('close');
            }
        },

        filters: {
            second(data) {
                if (data < 10) {
                    return '00:0' + data;
                } else {
                    return '00:' + data;
                }
            }
        }
    }
